.btn {
    display: flex;
    justify-content: center;
    align-items: center;
}

.btn button {
    padding: 5px 25px;
    margin: 2rem;
    border: none;
    color: #fff;
    font-size: 1.5rem;
    border-radius: 20px;
    background-color: rgb(24,24,24);
}

.btn button:focus {
    outline: none;
    color: rgb(24,24,24);
    background-color: rgb(138, 138, 138);
}

.btn input {
    padding: 5px 10px;
    border: 1px solid black;
}

.stn {
    display: flex;
    width: 567px;
    margin: auto;
    justify-content: center;
    align-items: center;
    text-align: center;
    color: #fff;
    font-weight: 700;
    text-transform: uppercase;
    -webkit-text-stroke-width: 2px;
    -webkit-text-stroke-color: black;
}

.stn img {
  position: relative;
  z-index: -1;
  max-height: 450px;
  padding: 2rem;
}

.top {
   position: absolute;
   bottom: 270px;
}

.bottom {
    position: absolute;
    bottom: -40px;
}